.topStory-slide-container {
  overflow: hidden; }
  .topStory-slide-container [data-swipeable] {
    overflow: hidden !important; }

.topStory-slide {
  position: relative;
  min-height: 240px;
  height: 100%;
  margin: 0 auto; }
  .topStory-slide-link, .topStory-slide-link:link, .topStory-slide-link:hover {
    display: block;
    width: 100%;
    padding: 10px;
    height: 100%;
    color: #ddd; }
  .topStory-slide-title {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.6);
    margin: 0;
    text-align: justify;
    font-size: 14px; }

@media screen and (min-width: 768px) {
  .topStory-slide {
    min-height: 400px; } }



