* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: Source Sans Pro,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,sans-serif; }

a,
a:link,
a:hover {
  display: inline-block;
  color: #3f51b5;
  text-decoration: none;
  word-break: break-all; }

.App-inner {
  max-width: 600px;
  margin: 0 auto;
  padding-bottom: 56px; }

@media screen and (min-width: 769px) and (max-width: 1024) {
  .App-inner {
    max-width: 100%; } }

@media screen and (min-width: 1921px) {
  .App-inner {
    max-width: 800px; } }

.list-subheader {
  top: 56px !important;
  background-color: #fff !important;
  -webkit-box-shadow: 0px 2px 4px -1px rgba(200, 200, 200, 0.2), 0px 4px 5px 0px rgba(200, 200, 200, 0.14), 0px 1px 10px 0px rgba(200, 200, 200, 0.12);
  box-shadow: 0px 2px 4px -1px rgba(200, 200, 200, 0.2), 0px 4px 5px 0px rgba(200, 200, 200, 0.14), 0px 1px 10px 0px rgba(200, 200, 200, 0.12); }

@media screen and (min-width: 600px) {
  .list-subheader {
    top: 64px !important; } }



